import {
  Card, Image, Row, Col,
} from 'antd';
import './i18n';
import { useTranslation } from 'react-i18next';

import joinImage from '@assets/portfolio/join.png';
import restoqImage from '@assets/portfolio/restoq.png';
import capacityImage from '@assets/portfolio/capacity.png';
import anchorioImage from '@assets/portfolio/anchorio.png';
import ProfileCard from '@components/ProfileCard';
import { portfolio } from '@services';

const Portfolio = () => {
  const { t } = useTranslation(['portfolio', 'translations']);

  const images = {
    join: joinImage,
    restoq: restoqImage,
    capacity: capacityImage,
    anchorio: anchorioImage,
  };

  return (
    <Row className="container content">
      <Col span={24} lg={5}>
        <ProfileCard />
      </Col>
      <Col span={24} lg={1} />
      <Col span={24} lg={18}>
        <Card type="inner" title={t(`portfolio:${portfolio.title}`).toUpperCase()}>
          <Image.PreviewGroup>
            <Row align="middle" justify="center" wrap>
              {portfolio.list.map(({ key, name }) => (
                <Col span={24} lg={12}>
                  <Card.Grid hoverable style={{ width: '100%' }}>
                    <Image
                      key={key}
                      alt={key}
                      width="100%"
                      src={images[key] || 'error'}
                      fallback="data:image/png;base64,"
                    />
                    <div><strong>{name}</strong></div>
                  </Card.Grid>
                </Col>
              ))}
            </Row>
          </Image.PreviewGroup>
        </Card>
      </Col>
    </Row>
  );
};

export default Portfolio;
